#section-contact .container {
	padding: 20% 15% 15%;
	display: flex;
	flex-direction: column;
}

#section-contact .container > div:first-child {
	display: flex;
	flex-direction: row;
	align-items: center;
	margin-bottom: 4rem;
}

#section-contact .container > div:first-child h3 {
	margin-right: 10px;
	font-size: 1.5rem;
	color: var(--primary-color);
	width: fit-content;
}

#section-contact .container > div:first-child hr {
	width: 50px;
	border: none;
	height: 2px;
	background-color: var(--light-color);
}

#section-contact #contacts-container > div {
	display: flex;
	flex-direction: row;
	margin-bottom: 1rem;
	align-items: center;
}

#section-contact #contacts-container > div .material-icons {
	color: var(--secondary-color);
	font-size: 1.8rem;
}

#section-contact #contacts-container img {
	height: 2rem;
}

#section-contact p,
#section-contact a {
	font-size: 1.2rem;
	color: white;
	margin-left: 10px;
}
