#section-skills .container {
	padding: 20% 15% 15%;
}

#section-skills .container > div:first-child {
	display: flex;
	flex-direction: row;
	align-items: center;
	margin-bottom: 4rem;
}

#section-skills .container > div:first-child h3 {
	margin-right: 10px;
	font-size: 1.5rem;
	color: var(--primary-color);
	width: min-content;
}

#section-skills .container > div:first-child hr {
	width: 50px;
	border: none;
	height: 2px;
	background-color: var(--light-color);
}

#section-skills .container h4 {
	font-size: 1.2rem;
	color: var(--secondary-color);
	text-align: center;
	margin-bottom: 2rem;
}

#section-skills .container .skills-container {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	grid-column-gap: 10px;
	grid-row-gap: 20px;
}

#section-skills .container > div:nth-of-type(2) {
	margin-bottom: 3rem;
}

#section-skills .container .skills-container > div {
	display: flex;
	flex-direction: column;
	align-items: center;
}

#section-skills .container .skills-container > div img {
	width: 100%;
}

#section-skills .container .skills-container > div p {
	font-size: 0.8rem;
	text-align: center;
	margin-top: 5px;
	color: var(--light-color);
}
