header .container {
	padding: 20% 15% 10%;
	display: flex;
	flex-direction: column;
}

header .container img {
	margin: 0 auto;
	width: 90%;
	margin-bottom: 40px;
	border: 3px solid var(--dark-color-two);
}

header div > p {
	color: var(--light-color);
	margin-bottom: 5px;
	line-height: 1.5rem;
}

header div > h2 {
	color: var(--primary-color);
	margin-bottom: 5px;
	font-size: 1.7rem;
}

header div > h3 {
	color: var(--secondary-color);
	margin-bottom: 10px;
}

header div button {
	margin-top: 2rem;
	border-radius: 5px;
	padding: 10px;
	width: fit-content;
	font-size: 1rem;
	border: 3px solid var(--secondary-color);
	color: var(--secondary-color);
}
