#section-projects .container {
	padding: 20% 15% 15%;
}

#section-projects .container > div:first-child {
	display: flex;
	flex-direction: row;
	align-items: center;
	margin-bottom: 4rem;
}

#section-projects .container > div:first-child h3 {
	margin-right: 10px;
	font-size: 1.5rem;
	color: var(--primary-color);
	width: fit-content;
}

#section-projects .container > div:first-child hr {
	width: 50px;
	border: none;
	height: 2px;
	background-color: var(--light-color);
}

#section-projects .container h4 {
	font-size: 1.2rem;
	color: var(--secondary-color);
	text-align: center;
	margin-bottom: 2rem;
}

#section-projects .container .card {
	width: 100%;
	padding: 5px 5px 10px;
	border-radius: 5px;
	background-color: var(--dark-color-two);
	display: flex;
	flex-direction: column;
	box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.1);
	margin-bottom: 1rem;
}

#section-projects .container .card img {
	border-radius: 5px;
	width: 100%;
	margin-bottom: 10px;
}

#section-projects .container .card p {
	width: 100%;
	padding: 0 5px;
	background-color: var(--dark-color-two);
	color: var(--light-color);
}

#section-projects .container .card a {
	padding: 0 5px;
	text-decoration: none;
	color: var(--secondary-color);
}

#section-projects .container button {
	background-color: var(--dark-color-two);
	color: var(--secondary-color);
	padding: 5px 5px 5px 10px;
	font-size: 1rem;
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: center;
	border-radius: 5px;
}

#section-projects .container button:not(:last-child) {
	margin-bottom: 3rem;
}

#section-projects .container .image-container {
	border-radius: 5px;
	padding: 5px;
	background-color: var(--dark-color-two);
	width: 100%;
	margin-bottom: 1rem;
	box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.1);
}
