/* desktop */
@media only screen and (min-width: 64.063em) {
	.container {
		width: 75%;
		margin: 0 auto;
	}

	/* navigation */
	nav#nav p:first-child {
		color: white;
	}

	nav#nav .container {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		flex-wrap: wrap;
		max-height: fit-content;
	}

	nav#nav .container .lg-display-none {
		display: none;
	}

	nav#nav .container div.lg-display-flex {
		display: flex;
		flex-direction: row;
		flex-wrap: nowrap;
		width: fit-content;
		gap: 10px;
	}

	nav#nav .container div hr {
		display: block;
	}

	nav#nav .container div a button {
		font-size: 1.2rem;
		padding: 10px 0;
	}
	/* header */
	header#header .container {
		padding: 10vh 0 15vh;
		flex-direction: row;
	}

	header#header img {
		margin: 0;
		width: 40%;
		margin-bottom: 0;
		border: 10px solid var(--dark-color-two);
	}

	header#header .container > div {
		padding-left: 2rem;
		display: flex;
		flex-direction: column;
		justify-content: center;
	}

	header#header div > p {
		font-size: 1.5rem;
		line-height: 2rem;
		margin-bottom: 2rem;
	}

	header#header div > h2 {
		color: var(--primary-color);
		margin-bottom: 0.5rem;
		font-size: 3rem;
	}

	header#header div > h3 {
		font-size: 2rem;
		margin-bottom: 2rem;
	}

	header#header div button {
		font-size: 2rem;
	}

	/* section aboutme */
	section#section-aboutme .container {
		padding: 10vh 0 15vh;
		display: flex;
		flex-direction: row;
	}

	section#section-aboutme .container > div {
		display: flex;
		flex-direction: column;
		justify-content: center;
	}

	section#section-aboutme .container .inner-container > div:first-child h3 {
		font-size: 2.5rem;
	}

	section#section-aboutme .container .inner-container {
		display: flex;
		flex-direction: column;
	}

	section#section-aboutme .container .inner-container > div {
		padding-right: 2rem;
	}

	section#section-aboutme .container .inner-container p {
		font-size: 1.5rem;
		line-height: 2rem;
		margin-bottom: 2rem;
	}
	section#section-aboutme .container img {
		display: block;
		margin: 0;
		width: 40%;
		margin-top: 0;
		border: 10px solid var(--dark-color-two);
	}

	/* section-skills*/
	section#section-skills .container {
		display: flex;
		flex-direction: column;
		padding: 10vh 0 15vh;
	}

	section#section-skills .container > div:first-child {
		align-self: center;
	}
	section#section-skills .container > div:first-child h3 {
		font-size: 2.5rem;
		width: fit-content;
	}

	section#section-skills .container h4 {
		font-size: 2rem;
		margin-bottom: 4rem;
	}

	section#section-skills .container .skills-container {
		grid-template-columns: repeat(6, 1fr);
		grid-column-gap: 2rem;
		grid-row-gap: 2rem;
	}

	section#section-skills .container .skills-container > div p {
		font-size: 1.5rem;
		text-align: center;
		margin-top: 1rem;
	}

	/* section projects */
	section#section-projects .container {
		padding: 10vh 0 15vh;
		display: flex;
		flex-direction: column;
	}

	section#section-projects .container > div:first-child {
		align-self: center;
	}
	section#section-projects .container > div:first-child h3 {
		font-size: 2.5rem;
	}

	section#section-projects .container h4 {
		font-size: 2rem;
		margin-bottom: 4rem;
	}

	section#section-projects .container #websites-container {
		display: grid;
		grid-template-columns: repeat(3, 1fr);
		grid-gap: 2rem;
	}

	section#section-projects .container .card p {
		font-size: 1.3rem;
		font-weight: normal;
	}

	section#section-projects .container .card a {
		font-size: 1rem;
	}

	section#section-projects .container #gallery-container {
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		justify-content: space-between;
		align-items: flex-start;
	}

	section#section-projects .container .image-container {
		width: 30%;
	}

	section#section-projects .container button {
		width: fit-content;
	}

	/* contact */
	section#section-contact .container {
		padding: 10vh 0 15vh;
		align-items: center;
		height: 100vh;
	}

	section#section-contact .container > div:first-child {
		align-self: center;
		margin-bottom: 5rem;
	}
	section#section-contact .container > div:first-child h3 {
		font-size: 2.5rem;
	}

	section#section-contact #contacts-container > div {
		margin-bottom: 3rem;
	}

	section#section-contact #contacts-container > div .material-icons {
		color: var(--secondary-color);
		font-size: 2.5rem;
	}

	section#section-contact p,
	section#section-contact a {
		font-size: 1.5rem;
	}

	/* css for hover effect */
}
