#section-aboutme .container {
	padding: 20% 15% 15%;
}

#section-aboutme .container .inner-container > div:first-child {
	margin-bottom: 4rem;
}

#section-aboutme .container .inner-container > div:first-child h3 {
	margin-right: 10px;
	font-size: 1.5rem;
	color: var(--primary-color);
	width: fit-content;
}

#section-aboutme .container .inner-container > div:first-child hr {
	width: 50px;
	border: none;
	height: 2px;
	background-color: var(--light-color);
}

#section-aboutme .container div div > p {
	line-height: 1.5rem;
	color: var(--light-color);
	margin-bottom: 1.5rem;
}

#section-aboutme .container div div > p span {
	color: var(--secondary-color);
}

#section-aboutme .container > img {
	display: block;
	margin: 0 auto;
	width: 90%;
	margin-top: 3rem;
	border: 3px solid var(--dark-color-two);
}
