nav {
	width: 100%;
	background-color: var(--dark-color);
}

nav p:first-child {
	color: white;
}

nav .container {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	flex-wrap: wrap;
}

nav .container .sm-display-block {
	display: block;
}

nav .container .sm-display-flex {
	display: flex;
}

nav .container .sm-display-none {
	display: none;
}

nav .container .material-icons {
	font-size: 2rem;
	padding: 10px 10px;
	color: var(--secondary-color);
}

nav .container > img {
	display: block;
	height: 30px;
	margin: 10px 10px;
}

nav .container > div {
	width: 100%;
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	border-top: 1px solid var(--dark-color-two);
}

nav .container div a {
	display: block;
	width: 100%;
}

nav .container div a button {
	width: 100%;
	padding: 15px 0;
	color: var(--light-color);
	display: flex;
	flex-direction: column;
	align-items: center;
	gap: 3px;
	font-size: 1rem;
}

nav .container hr {
	display: none;
	width: 20px;
	border: none;
	height: 2px;
}

nav .container button.active hr {
	background-color: var(--secondary-color);
}
