@import url("https://fonts.googleapis.com/css2?family=Ubuntu:wght@300;400;500;700&display=swap");

:root {
	--primary-color: #49beb7;
	--secondary-color: #e6873c;
	--light-color: #eee;
	--dark-color: #333;
	--dark-color-two: #444;
}
* {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
	font-family: "Ubuntu", sans-serif;
}

html {
	scroll-behavior: smooth;
}

body {
	background-color: var(--dark-color);
}

.container {
	width: 100%;
}

button {
	background: none;
	outline: none;
	border: none;
	cursor: pointer;
	font-size: 0.8rem;
}

button:hover {
	opacity: 0.8;
}

.nav-sticky {
	position: absolute;
	visibility: hidden;
	top: 0;
	left: 0;
	right: 0;
	top: 100vh;
}

.sticky {
	visibility: visible;
	z-index: 5;
	box-shadow: 0 1px 1px rgba(255, 255, 255, 0.1);
}

a {
	text-decoration: none;
}
