@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@300;400;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
	--primary-color: #49beb7;
	--secondary-color: #e6873c;
	--light-color: #eee;
	--dark-color: #333;
	--dark-color-two: #444;
}
* {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
	font-family: "Ubuntu", sans-serif;
}

html {
	scroll-behavior: smooth;
}

body {
	background-color: #333;
	background-color: var(--dark-color);
}

.container {
	width: 100%;
}

button {
	background: none;
	outline: none;
	border: none;
	cursor: pointer;
	font-size: 0.8rem;
}

button:hover {
	opacity: 0.8;
}

.nav-sticky {
	position: absolute;
	visibility: hidden;
	top: 0;
	left: 0;
	right: 0;
	top: 100vh;
}

.sticky {
	visibility: visible;
	z-index: 5;
	box-shadow: 0 1px 1px rgba(255, 255, 255, 0.1);
}

a {
	text-decoration: none;
}

/* desktop */
@media only screen and (min-width: 64.063em) {
	.container {
		width: 75%;
		margin: 0 auto;
	}

	/* navigation */
	nav#nav p:first-child {
		color: white;
	}

	nav#nav .container {
		display: -webkit-flex;
		display: flex;
		-webkit-flex-direction: row;
		        flex-direction: row;
		-webkit-justify-content: space-between;
		        justify-content: space-between;
		-webkit-flex-wrap: wrap;
		        flex-wrap: wrap;
		max-height: -webkit-fit-content;
		max-height: -moz-fit-content;
		max-height: fit-content;
	}

	nav#nav .container .lg-display-none {
		display: none;
	}

	nav#nav .container div.lg-display-flex {
		display: -webkit-flex;
		display: flex;
		-webkit-flex-direction: row;
		        flex-direction: row;
		-webkit-flex-wrap: nowrap;
		        flex-wrap: nowrap;
		width: -webkit-fit-content;
		width: -moz-fit-content;
		width: fit-content;
		grid-gap: 10px;
		gap: 10px;
	}

	nav#nav .container div hr {
		display: block;
	}

	nav#nav .container div a button {
		font-size: 1.2rem;
		padding: 10px 0;
	}
	/* header */
	header#header .container {
		padding: 10vh 0 15vh;
		-webkit-flex-direction: row;
		        flex-direction: row;
	}

	header#header img {
		margin: 0;
		width: 40%;
		margin-bottom: 0;
		border: 10px solid var(--dark-color-two);
	}

	header#header .container > div {
		padding-left: 2rem;
		display: -webkit-flex;
		display: flex;
		-webkit-flex-direction: column;
		        flex-direction: column;
		-webkit-justify-content: center;
		        justify-content: center;
	}

	header#header div > p {
		font-size: 1.5rem;
		line-height: 2rem;
		margin-bottom: 2rem;
	}

	header#header div > h2 {
		color: var(--primary-color);
		margin-bottom: 0.5rem;
		font-size: 3rem;
	}

	header#header div > h3 {
		font-size: 2rem;
		margin-bottom: 2rem;
	}

	header#header div button {
		font-size: 2rem;
	}

	/* section aboutme */
	section#section-aboutme .container {
		padding: 10vh 0 15vh;
		display: -webkit-flex;
		display: flex;
		-webkit-flex-direction: row;
		        flex-direction: row;
	}

	section#section-aboutme .container > div {
		display: -webkit-flex;
		display: flex;
		-webkit-flex-direction: column;
		        flex-direction: column;
		-webkit-justify-content: center;
		        justify-content: center;
	}

	section#section-aboutme .container .inner-container > div:first-child h3 {
		font-size: 2.5rem;
	}

	section#section-aboutme .container .inner-container {
		display: -webkit-flex;
		display: flex;
		-webkit-flex-direction: column;
		        flex-direction: column;
	}

	section#section-aboutme .container .inner-container > div {
		padding-right: 2rem;
	}

	section#section-aboutme .container .inner-container p {
		font-size: 1.5rem;
		line-height: 2rem;
		margin-bottom: 2rem;
	}
	section#section-aboutme .container img {
		display: block;
		margin: 0;
		width: 40%;
		margin-top: 0;
		border: 10px solid var(--dark-color-two);
	}

	/* section-skills*/
	section#section-skills .container {
		display: -webkit-flex;
		display: flex;
		-webkit-flex-direction: column;
		        flex-direction: column;
		padding: 10vh 0 15vh;
	}

	section#section-skills .container > div:first-child {
		-webkit-align-self: center;
		        align-self: center;
	}
	section#section-skills .container > div:first-child h3 {
		font-size: 2.5rem;
		width: -webkit-fit-content;
		width: -moz-fit-content;
		width: fit-content;
	}

	section#section-skills .container h4 {
		font-size: 2rem;
		margin-bottom: 4rem;
	}

	section#section-skills .container .skills-container {
		grid-template-columns: repeat(6, 1fr);
		grid-column-gap: 2rem;
		grid-row-gap: 2rem;
	}

	section#section-skills .container .skills-container > div p {
		font-size: 1.5rem;
		text-align: center;
		margin-top: 1rem;
	}

	/* section projects */
	section#section-projects .container {
		padding: 10vh 0 15vh;
		display: -webkit-flex;
		display: flex;
		-webkit-flex-direction: column;
		        flex-direction: column;
	}

	section#section-projects .container > div:first-child {
		-webkit-align-self: center;
		        align-self: center;
	}
	section#section-projects .container > div:first-child h3 {
		font-size: 2.5rem;
	}

	section#section-projects .container h4 {
		font-size: 2rem;
		margin-bottom: 4rem;
	}

	section#section-projects .container #websites-container {
		display: grid;
		grid-template-columns: repeat(3, 1fr);
		grid-gap: 2rem;
	}

	section#section-projects .container .card p {
		font-size: 1.3rem;
		font-weight: normal;
	}

	section#section-projects .container .card a {
		font-size: 1rem;
	}

	section#section-projects .container #gallery-container {
		display: -webkit-flex;
		display: flex;
		-webkit-flex-direction: row;
		        flex-direction: row;
		-webkit-flex-wrap: wrap;
		        flex-wrap: wrap;
		-webkit-justify-content: space-between;
		        justify-content: space-between;
		-webkit-align-items: flex-start;
		        align-items: flex-start;
	}

	section#section-projects .container .image-container {
		width: 30%;
	}

	section#section-projects .container button {
		width: -webkit-fit-content;
		width: -moz-fit-content;
		width: fit-content;
	}

	/* contact */
	section#section-contact .container {
		padding: 10vh 0 15vh;
		-webkit-align-items: center;
		        align-items: center;
		height: 100vh;
	}

	section#section-contact .container > div:first-child {
		-webkit-align-self: center;
		        align-self: center;
		margin-bottom: 5rem;
	}
	section#section-contact .container > div:first-child h3 {
		font-size: 2.5rem;
	}

	section#section-contact #contacts-container > div {
		margin-bottom: 3rem;
	}

	section#section-contact #contacts-container > div .material-icons {
		color: var(--secondary-color);
		font-size: 2.5rem;
	}

	section#section-contact p,
	section#section-contact a {
		font-size: 1.5rem;
	}

	/* css for hover effect */
}

header .container {
	padding: 20% 15% 10%;
